.approaches {
  &__item {
    position: relative;
    &:nth-child(1) {
      background-color: $beige;
      &:after {
        content: '';
        position: absolute;
        background-image: url("../../images/app/Union-1.svg");
        right: -12px;
        top: 50%;
        transform: translate(100%,-50%);
        width: 16px;
        height: 12px;
        background-repeat: no-repeat;
        background-size: contain;
        @media (min-width: 1024px) {
          width: 22px;
          height: 16px;
          right: -35px;
        }
      }
    }
    &:nth-child(2) {
      background-color: $beige-light;
      &:after {
        content: '';
        position: absolute;
        background-image: url("../../images/app/Union-2.svg");
        right: 50%;
        bottom: -12px;
        transform: translate(50%, 100%);
        width: 12px;
        height: 16px;
        background-repeat: no-repeat;
        background-size: contain;
        @media (min-width: 1024px) {
          width: 16px;
          height: 22px;
          bottom: -35px;
        }
      }
    }
    &:nth-child(3) {
      background-color: $blue-light;
      &:after {
        content: '';
        position: absolute;
        background-image: url("../../images/app/Union-4.svg");
        width: 12px;
        height: 16px;
        left: 50%;
        top: -12px;
        transform: translate(-50%, -100%);
        background-repeat: no-repeat;
        background-size: contain;
        @media (min-width: 1024px) {
          width: 16px;
          height: 22px;
          top: -35px;
        }
      }
    }
    &:nth-child(4) {
      background-color: $blue;
      &:after {
        content: '';
        position: absolute;
        background-image: url("../../images/app/Union-3.svg");
        width: 16px;
        height: 12px;
        left: -12px;
        top: 50%;
        transform: translate(-100%, -50%);
        background-repeat: no-repeat;
        background-size: contain;
        @media (min-width: 1024px) {
          width: 22px;
          height: 16px;
          left: -35px;
        }
      }
    }
  }
}