.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  &__line {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 1px;
    display: flex;
    & > div:nth-child(1) {
      overflow: hidden;
      position: relative;
      width: 50%;
      height: 1px;
      &:after {
        content: '';
        max-width: 100%;
        height: 1px;
        background-color: $beige;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        transition: transform .5s;
      }
    }
    & > div:nth-child(2) {
      width: 50%;
      height: 1px;
      overflow: hidden;
      position: relative;
      &:after {
        content: '';
        max-width: 100%;
        height: 1px;
        background-color: $beige;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        transition: transform .5s;
      }
    }
    &.pined {
      & > div:nth-child(1) {
        &:after {
          transform: translateX(100%);
        }
      }
      & > div:nth-child(2) {
        &:after {
          transform: translateX(-100%);
        }
      }
    }
  }
  &__menu {
    ul {
      display: none;
      @media (min-width: 1024px) {
        display: flex;
        gap: 81px;
        align-items: center;
      }
      a {
        font-size: 18px;
        line-height: 144%;
        font-weight: 400;
        color: $black;
        position: relative;
        overflow: hidden;
        display: flex;
        &:hover:after {
          transform: translateX(0);
        }
        &:after {
          content: '';
          width: 100%;
          height: 1px;
          background-color: $black;
          position: absolute;
          left: 0;
          bottom: 0;
          transform: translateX(-105%);
          transition: .5s;
        }
      }
    }
  }
  .burger {
    display: flex;
    @media (min-width: 1024px) {
      display: none;
    }
  }
}