.footer {
  margin-top: 60px;
  @media (min-width: 1024px) {
    margin-top: 160px;
  }
  &__social {
    position: relative;
    .footer__social-original {
      transition: .3s;
    }
    .footer__social-hover {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      transition: .3s;
    }
    &:hover {
      .footer__social-original {
        opacity: 0;
      }
      .footer__social-hover {
        opacity: 1;
      }
    }
  }
  form {
    .wpcf7-not-valid-tip {
      font-family: "Manrope",sans-serif;
      color: #ff521c;
      font-weight: 600;
      font-size: 14px;
    }
    .wpcf7-spinner {
      display: none;
    }
    p {
      display: flex;
      gap: 31px;
      flex-direction: column;
      @media (min-width: 1024px) {
        gap: 40px;
      }
    }
  }
  &__inputs {
    padding: 9px 0;
    width: 100%;
    background-color: transparent;
    font-size: 20px;
    line-height: 180%;
    font-weight: 400;
    color: $white;
    border-bottom: 1px solid $beige-light;
    &::placeholder {
      color: #d4d4d2;
    }
    &.wpcf7-not-valid {
      color: #FF521C;
      border-bottom: 1px solid #FF521C;
    }
  }
  &__menu {
    margin-top: 44px;
    margin-bottom: 40px;
    @media (min-width: 1024px) {
      margin: 0;
      display: flex;
      align-items: flex-end;
    }
    ul {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
      @media (min-width: 1024px) {
        align-items: start;
      }
      a {
        color: $white;
        font-size: 18px;
        line-height: 144%;
        font-weight: 400;
        position: relative;
        overflow: hidden;
        display: flex;
        @media (min-width: 1024px) {
          &:hover:after {
            transform: translateX(0);
          }
        }
        &:after {
          content: '';
          width: 100%;
          height: 1px;
          background-color: $white;
          position: absolute;
          left: 0;
          bottom: 0;
          transform: translateX(-105%);
          transition: .5s;
        }
      }
    }
  }
  &__form-submit {
    margin: 0 auto;
    @media (min-width: 1024px) {
      margin: 0;
      margin-right: auto;
      font-size: 22px;
    }
  }
  &__pop-thanks {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    transition: .3s;
    &.active {
      pointer-events: visible;
      opacity: 1;
    }
  }
  &__form {
    transition: .5s;
    &.hide {
      opacity: 0;
      pointer-events: none;
    }
  }
}