.courses {
  &__ul {
    margin-top: 19px;
    padding: 0 20px;
    @media (min-width: 1024px) {
      margin-top: 40px;
      padding: 0 49px;
    }
    ul {
      display: flex;
      flex-direction: column;
      gap: 11px;
      li {
        display: flex;
        gap: 20px;
        align-items: center;
        font-size: 18px;
        line-height: 144%;
        padding-bottom: 8px;
        border-bottom: 1px solid $white;
        @media (min-width: 1024px) {
          font-size: 20px;
          line-height: 180%;
        }
        &:before {
          content: '';
          width: 10px;
          height: 10px;
          background-color: $black;
          border-radius: 50%;
          flex-shrink: 0;
        }
      }
    }
  }
}