.mobile__menu {
  transform: translateY(-100%);
  transition: .5s;
  &.opened {
    transform: translateY(0);
  }
  &-ul {
    margin-top: 91px;
    ul {
      display: flex;
      flex-direction: column;
      gap: 40px;
      align-items: center;
      opacity: 0;
      transition: .5s;
    }
    li {
      font-size: 38px;
      line-height: 129%;
      font-weight: 400;
      color: $white;
    }
    &.active {
      ul {
        opacity: 1;
      }
    }
  }
  &-top {
    opacity: 0;
    transition: .5s;
    position: relative;
    &:after {
      content: '';
      width: 100%;
      height: 1px;
      background-color: $beige;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    &.active {
      opacity: 1;
    }
  }
}